import '../scssFile/linhvuc.css'
import React, { useEffect, useState } from "react";
import { SubjectSlider } from "../Component/SubjectSlider";
import { CourseSlider } from "../Component/CourseSlider";
import { Course } from "../Component/commonComponent/Course";
import { useSelector } from "react-redux";
import Axios from "axios";
import { nodeApiLink } from "../config/NodeServerLink";
import { CourseSkelethon } from "../Component/CourseSkelethon";
import { useTranslation, Trans } from "react-i18next";

export const Linhvuc = (props) => {
    const [listCourse, setListCourse] = useState([]);
    const [loading, setLoading] = useState(true);
    const auth = useSelector((state) => state.userStatus.auth);
    const { t } = useTranslation("translation");
    useEffect(() => {
        const fetchCourse = async () => {
            setLoading(true);
            const Courses = await Axios.get(`${nodeApiLink}/api/course/get/linhvuc/${props.match.params.linhvuc}`);
            setListCourse(Courses.data);
            setLoading(false);
        };
        fetchCourse();
        return () => {
            //
        };
    }, [auth, props.match.params.linhvuc]);
    return (
        <div className="container-fluid">
            <div className="px-md-3">
                <div className="Popular-course">
                    <h3 className="px-3"><Trans t={t}>Các khóa học phổ biến</Trans></h3>
                    <CourseSlider
                        type="course"
                        courses={listCourse?.length > 0 ? listCourse.slice(0, 11) : []}
                        loading={loading}
                    />
                </div>
            </div>
            <div className="px-md-3">
                <div className="Popular-course">
                    <h3 className="px-3"><Trans t={t}>Toàn bộ khóa học liên quan</Trans></h3>
                    <div className="row card-columns discovery-card-list">
                        {!loading ? (
                            listCourse.map((course) => {
                                return <Course key={course._id} data={course} xs={6} md={3} />;
                            })
                        ) : (
                            <>
                                <CourseSkelethon customXs={6} customMd={3} />
                                <CourseSkelethon customXs={6} customMd={3} />
                                <CourseSkelethon customXs={6} customMd={3} />
                                <CourseSkelethon customXs={6} customMd={3} />
                            </>
                        )}
                    </div>
                </div>
            </div>
            <div className="px-3">
                <div className="subjects">
                    <h3><Trans t={t}>Khám phá</Trans></h3>
                    <SubjectSlider />
                </div>
            </div>
        </div>
    );
};
export default Linhvuc;
