import React, { useEffect, useState, lazy, memo } from "react";
import CourseSlider from "../Component/CourseSlider";
import { useSelector } from "react-redux";
import Axios from "axios";
import { nodeApiLink } from "../config/NodeServerLink";
import  TopSubject  from "../Component/TopSubject";
import { Trans, useTranslation } from "react-i18next";
const LandingBanner = lazy(() => import("../Component/LandingBanner"));
const SubjectSlider = lazy(() => import("../Component/SubjectSlider"));
const PartnerLogos = lazy(() => import("../Component/PartnerLogos"));
export const UserHome = (props) => {
    const { t } = useTranslation("translation");
    const [listCourse, setListCourse] = useState([]);
    const [loading, setLoading] = useState(true);
    const auth = useSelector((state) => state.userStatus.auth);
    useEffect(() => {
        const fetchCourse = async () => {
            setLoading(true);
            const Courses = await Axios.get(`${nodeApiLink}/api/course/get`);
            setListCourse(Courses.data);
            setLoading(false);
        };
        fetchCourse();
        return () => {
            //
        };
    }, [auth]);
    return (
        <div>
            <div className="container-fluid">
                {!auth && <LandingBanner />}
                <TopSubject />
                <div className="px-md-3">
                    <div className="Popular-course">
                        <h3>
                            <Trans t={t}>Các khoá học phổ biến</Trans>
                        </h3>
                        <CourseSlider
                            type="course"
                            courses={listCourse?.length > 0 ? listCourse.slice(0, 11) : []}
                            loading={loading}
                        />
                    </div>
                </div>
                <div className="px-md-3">
                    <div className="Popular-course">
                        <h3>
                            <Trans t={t}>Các chứng chỉ phổ biến</Trans>
                        </h3>
                        <CourseSlider
                            type="course"
                            courses={listCourse?.length > 0 ? listCourse.slice(11) : []}
                            loading={loading}
                        />
                    </div>
                </div>
                <div className="px-md-3">
                    <div className="subjects">
                        <h3>
                            <Trans t={t}>Khám phá</Trans>
                        </h3>
                        <SubjectSlider />
                    </div>
                </div>
            </div>
            <PartnerLogos />
        </div>
    );
};
export default memo(UserHome);
