import React, { memo } from "react";
import {
    CardActionArea,
    CardMedia,
    CardContent,
    Typography,
    IconButton,
    CardActions,
    Card,
    makeStyles,
    Zoom,
    Grid,
} from "@material-ui/core";
import { Share, AddShoppingCart, FavoriteBorderOutlined, RemoveShoppingCartRounded } from "@material-ui/icons";
import { Link } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import { addCartItem, removeCartItem } from "../../action/userAction";

export const Course = (props) => {
    const { data, xs, md } = props;
    const userInfo = useSelector((state) => state.userStatus.userInfo);
    const dispatch = useDispatch();
    const useStyle = makeStyles((theme) => ({
        root: {
            position: "relative",
            margin:10,
            borderRadius: 20,
            boxShadow: "0 2px 20px -4px rgba(0,0,0,.19)",
            transformStyle: "preserve-3d",
            transition: "all .2s ease-out",
            [theme.breakpoints.down("md")]: {
                margin: theme.spacing(1),
            },
            "& h4": {   
                [theme.breakpoints.down("md")]: {
                    fontSize: "1.1rem",
                },
            },
            "&:hover": {
                transform: "translateY(2px)",
                boxShadow: "0 2px 5px rgba(0,0,0,.1), 0 1px 2px rgba(0,0,0,.05)",
            },
        },
        media: {
            height: 168,
            [theme.breakpoints.down("md")]: {
                height: 120,
            },
        },
        title: {
            transform: "translateZ(20px)",
            transformStyle: "preserve-3d",
            marginTop: theme.spacing(4),
            height: 116,
            [theme.breakpoints.down("md")]: {
                marginTop: theme.spacing(0),
                fontSize: "1rem",
            },
        },
        courseName: {
            display:'-webkit-box',
            minHeight:64,
            WebkitBoxOrient:'vertical',
            WebkitLineClamp:2,
            textOverflow: "ellipsis",
            overflow: "hidden",
        },
        uni: {
            whiteSpace: "nowrap",
            textOverflow: "ellipsis",
            overflow: "hidden",
        },
        fab: {
            position: "absolute",
            bottom: theme.spacing(1),
            right: theme.spacing(1),
        },
    }));
    const addCartHandler = (courseId) => {
        dispatch(addCartItem(courseId));
    };
    const removeCartItemHandler = (id) => {
        dispatch(removeCartItem(id));
    };
    const classes = useStyle();
    return (
        <Grid xs={xs ? xs : 12} md={md ? md : 12} item={true}>
                <Card className={classes.root}>
                    <Link style={{ color: "black" }} to={`/detail/${data._id}`}>
                        <CardActionArea>
                            <CardMedia className={classes.media} image={data.pic} src={data.pic} />
                            <CardContent className={classes.title}>
                                <Typography variant="h5" component="h4" className={classes.courseName}>
                                    {data.title}
                                </Typography>
                                <Typography variant="body2" component="p" className={classes.uni}>
                                    {data.university}
                                </Typography>
                            </CardContent>
                        </CardActionArea>
                    </Link>
                    <CardActions disableSpacing>
                        <IconButton>
                            <FavoriteBorderOutlined />
                        </IconButton>
                        <IconButton>
                            <Share />
                        </IconButton>
                        {userInfo ? (
                            <>
                                <Zoom in={userInfo.cart.includes(data._id)}>
                                    <IconButton
                                        className={classes.fab}
                                        onClick={() => {
                                            removeCartItemHandler(data._id);
                                        }}
                                        style={{ marginLeft: "auto" }}
                                    >
                                        <RemoveShoppingCartRounded />
                                    </IconButton>
                                </Zoom>
                                <Zoom in={!userInfo.cart.includes(data._id)}>
                                    <IconButton
                                        className={classes.fab}
                                        onClick={() => {
                                            addCartHandler(data._id);
                                        }}
                                        style={{ marginLeft: "auto" }}
                                    >
                                        <AddShoppingCart />
                                    </IconButton>
                                </Zoom>
                            </>
                        ) : (
                            ""
                        )}
                    </CardActions>
                </Card>
        </Grid>
    );
};
export default memo(Course)